package pl.michal_cyran.portfolio.projects.presentation

import androidx.compose.runtime.Composable

import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.height
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.compose.ui.modifiers.size
import com.varabyte.kobweb.silk.components.graphics.Image
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.H2
import org.jetbrains.compose.web.dom.H6
import org.jetbrains.compose.web.dom.Text
import pl.michal_cyran.portfolio.projects.data.projects
import pl.michal_cyran.portfolio.skils.data.skillsBySlug
import kotlin.text.iterator

@Composable
fun ProjectDetailsScreen(
    projectSlug: String,
) {
    val project = projects.find { it.slug == projectSlug }
        ?: throw IllegalArgumentException("Project not found: $projectSlug")

//    var screenshotToDisplay by remember { mutableStateOf("") }

    Column(
        verticalArrangement = Arrangement.spacedBy(20.px),
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = Modifier
            .fillMaxWidth(100.percent)
    ) {
        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.spacedBy(16.px),
        ) {
            Image(
                src = project.logo,
                alt = "Project Logo",
                modifier = Modifier.size(60.px),
            )

            Text(
                project.name,
//                style = MaterialTheme.typography.headlineLarge,
//                modifier = padding(16.px)
            )
        }

        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.spacedBy(8.px),
            modifier = Modifier.padding(bottom = 16.px)
        ) {
            TypeBadge(
                type = project.type
            )

            CategoryBadge(
                category = project.category
            )
        }

        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.spacedBy(16.px),
            modifier = Modifier.padding(bottom = 16.px)
        ) {
            project.links.forEach { link ->
                ProjectLinkButton(link = link)
            }
        }

        Box(
            modifier = Modifier
                .fillMaxWidth(60.percent)
                .padding(16.px),
//            colors = CardDefaults.cardColors(
//                containerColor = MaterialTheme.colorScheme.surfaceContainer
//            )
        ) {
            Column(
                modifier = Modifier.padding(40.px)
            ) {

                H2 {
                    Text(Res.string.project_overview)
                }

                H6 {
                    Text(
                        project.description,
                    )
                }

            }
        }

        Row(
            modifier = Modifier
                .fillMaxWidth(80.percent)
                .padding(16.px),
            horizontalArrangement = Arrangement.spacedBy(16.px),
            verticalAlignment = Alignment.CenterVertically
        ) {
            project.skills.forEach { slug ->
                val skill = skillsBySlug[slug]!!

//                val interactionSource = remember { MutableInteractionSource() }
//                val isHovered by interactionSource.collectIsHoveredAsState()
//                val scale by animateFloatAsState(
//                    targetValue = if (isHovered) 1.05f else 1f,
//                    label = "CardScale"
//                )
//                val color = if (isHovered) {
//                    MaterialTheme.colorScheme.primaryContainer
//                } else {
//                    MaterialTheme.colorScheme.surfaceContainer
//                }
                Box(
                    modifier = Modifier
                        .weight(1f)
                ) {
                    Column(
                        horizontalAlignment = Alignment.CenterHorizontally,
                        verticalArrangement = Arrangement.spacedBy(16.px),
                        modifier = Modifier.padding(20.px).fillMaxWidth()
                    ) {
                        Image(
                            skill.icon,
//                            contentDescription = "Skill Icon",
                            modifier = Modifier
                                .size(50.px)
                        )
                        Text(
                            skill.name,
//                            style = MaterialTheme.typography.titleMedium,
//                            modifier = padding(8.px)
                        )
                    }


                }

                Box(
                    modifier = Modifier.height(8.px)
                )
            }
        }

        if (project.screenshots.isEmpty()) {
            return@Column
        }

        Box(
            modifier = Modifier
                .fillMaxWidth(60.percent)
                .padding(16.px),
//            colors = CardDefaults.cardColors(
//                containerColor = MaterialTheme.colorScheme.surfaceContainer
//            )
        ) {
            Column(
                modifier = Modifier.padding(40.px)
            ) {
                Text(
                    Res.string.screenshots,
//                    style = MaterialTheme.typography.titleMedium,
//                    modifier = Modifier.padding(bottom = 8.px)
//                )
                )
            }


            for (i in project.screenshots.indices step 2) {
                Row(
                    horizontalArrangement = Arrangement.spacedBy(16.px),
                    modifier = Modifier.fillMaxWidth().padding(topBottom = 20.px)
                ) {
                    project.screenshots.subList(i, (i + 2).coerceAtMost(project.screenshots.size)).forEach { screenshot ->
                        Screenshot(
                            screenshot.resource,
                            modifier = Modifier.weight(1f),
                            screenshotText = screenshot.label,
                        )
                    }
                    repeat(2 - (project.screenshots.size - i).coerceAtMost(2)) {
                        Box(modifier = Modifier.weight(1f))
                    }
                }
            }
        }
    }

}