@Composable
fun ExperienceScreen() {
    Column(
        modifier = Modifier
            .fillMaxSize()
            .padding(16.px),
        horizontalAlignment = Alignment.CenterHorizontally,
    ) {
        H1 {
            Text(
                Res.string.experience_title
            )
        }

        H4 {
            Text(
                Res.string.experience_subtitle
            )
        }

        for (experience in experiences) {
            ExperienceCard(
                experience = experience,
            )
        }
    }
}



