@Composable
fun ProjectCard(
    project: Project,
    onProjectClick: () -> Unit,
    modifier: Modifier = Modifier
) {
    Box(
        modifier = modifier
            .then(backgroundContainer.toModifier())
            .onClick { onProjectClick() }
            .cursor(Cursor.Pointer)

    ) {
        Column(
            modifier = Modifier.padding(20.px).fillMaxHeight().fillMaxWidth(),
            horizontalAlignment = Alignment.CenterHorizontally,
        ) {
            Row(
                modifier = Modifier.fillMaxWidth(),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.spacedBy(10.px)
            ) {
                Box(
                    modifier = Modifier
                        .size(64.px),
//                        .background(MaterialTheme.colorScheme.surfaceContainerLow, CircleShape),
                    contentAlignment = Alignment.Center
                ) {
                    Image(
                        src = project.logo,
                        alt = "Project Logo",
                        modifier = Modifier.size(60.px),
                    )
                }

                TypeBadge(
                    type = project.type
                )
            }


            H4 {
                Text(
                    project.name
                )
            }


            H6 {
                Text(
                    project.shortDescription
                )
            }

            Spacer()

            HorizontalDivider(
                modifier = Modifier.padding(10.px)
            )

            Row(
                modifier = Modifier.fillMaxWidth().padding(leftRight = 20.px, topBottom = 8.px),
                horizontalArrangement = Arrangement.spacedBy(8.px),
                verticalAlignment = Alignment.CenterVertically
            ) {
                project.skills.forEach { slug ->
                    val skill = skillsBySlug[slug]!!
                    Image(
                        src = skill.icon,
                        alt = skill.name,
                        modifier = Modifier
                            .size(50.px)
                            .border(color = Colors.Cyan, width = 1.px)
                            .borderRadius(8.px)
                            .then(backgroundContainer.toModifier())
                    )
                }
            }
        }
    }
}